body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


*, html {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-size: 16px;
}

:root {
  scroll-behavior: smooth;
}




  .header {
    height: 60vh;
    background-image: url("https://images.unsplash.com/photo-1623718649591-311775a30c43?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1170&q=80");
    background-size:cover;
  }

  .header::after {
    content: '';
    background-color: rgba(0, 0, 0, 0.5);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    height: 60vh;
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
}

.texto-encabezado {
  padding-top: 20px;
  z-index: 10;
  position: relative;
}
  
  
  .texto-encabezado > h1 {
    font-family: "Kaisei Opti", serif;
    background: -webkit-linear-gradient(#d9edf5,#2d9bce);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    color: #17738f;  
    font-size: 3em;
    text-align: center;
    padding: 20px 0px;
  }

  .texto-encabezado > h3 {
    font-family: "Kaisei Opti", serif;
    color: #bcd3dd; 
    font-size: 1.6em;
    padding: 6px;
    text-align: center;
  }

.filtros {
    background-color: #edf6f9;
    height: 15vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
.filtros-lista {
    display: flex;
    width: 100%;
    justify-content: space-around;
    align-items: center;
    overflow: hidden;
    max-width: 960px;
  }

.fecha span {
    padding: 0px 5px;
  }

.fecha {
    display: flex;
  }

.bx {
    font-size: 1.3em;
  }

.bxs-calendar {
    font-size: 1.4em;
    position: relative;
    top: -1px;
  }

.contenedor-select {
    display: flex;
    margin-left: 5px;
  }

.contenedor-select .bx {
    padding-right: 3px;
  }

input, select {
    max-height: 35px;
    height: 3.2vh;
    cursor: pointer;
    border-radius: 5px;
    background-color:#f8f8f8;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 0.8em;
  }

input {
    width: 13vw;
    max-width: 150px;
    padding: 10px;
}

select {
    width: 10vw;
    max-width: 100px;
    padding: 0px 5px;
}

select > option {
    font-size: 1em;
}

button {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px 12px;
    border-radius: 3px;
    text-decoration: none;
    font-family: sans-serif;
    font-weight: 400;
    color:#f0ebeb;
    font-size: 0.9em;
    background-color:#0a7099;
    text-align: center;
    transition: all 0.2s;
    margin-left: 20px;
    cursor:pointer;
  }

button .bx {
    padding-right: 10px;
  }

button:hover {
    background-color:#0a7cad;
    cursor: pointer;
  }

@media all and (max-width:30em) {
    button {
        display:block;
        margin:2px auto;
        }
   }

 .hoteles {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .contenedor-hoteles {
    margin-top: 25px;
    max-width: 1000px;
    min-width: 860px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: stretch;
  }



.hotel {
  width: 30%;
  min-width: 250px;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
  border-radius: 10px;
  margin: 10px;
  padding: 8px 5px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
  
.hotel h1 {
  font-size: 1.5em;
  background-color: #90e4d9;
  font-family:"Kaisei Opti", serif;
  text-align: center;
  border-radius: 8px;
  margin: 5px 0px ;
  padding: 5px 0px;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
}

.hotel h3 {
  text-align: center;
  margin: 5px 0px;
  font-size: 1.1em;
}
 
.hotel .texto {
  max-height: 120px;
  overflow-y: scroll;
  margin: 10px;
}

.hotel > ::-webkit-scrollbar {
  width: 8px;
}

/* Track */
.hotel > ::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.4);
  border-radius: 10px;
}

/* Handle */
.hotel > ::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: #90e4d9; 
  --webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.4); 
} 

.hotel h4 {
  background-color: #edf6f9;
  padding: 8px 10px;
  border-radius: 5px;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 0px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
}

.fechas-hotel h4 {
  margin-bottom: 12px;
}

.hotel > button {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.3em;
  font-weight: 600;
  padding: 7px 0px;
  margin: 5px;
  margin-bottom: 10px;
  background-color: #90e4d9;
  color: black;
  border-color: #83ddd1;
  font-family: "Roboto", sans-serif;
  border-radius: 5px;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
}

.hotel .pre-hab {
  padding: 0px 10px 10px 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Habitaciones {
  display: flex;
  font-size: 1.1em;
  background-color: #4361ee;
  padding: 15px 5px;
  font-weight: 600;
  color: #FFFF;
  box-shadow: rgba(17, 17, 26, 0.1) 4px 4px 4px, rgba(17, 17, 26, 0.3) 0px 0px 7px;
}

.cama-icono {
  padding-right: 5px;
  position: relative;
  top: 2px;
}

.signo-dolar {
  color: #ebbc24;
  padding: 15px 5px 15px 0px;
}

.signo-dolar .bx-dollar {
  font-weight: 700;
  font-size: 1.4em;
}

.signo-dolar .bx-plomo {
  color: rgba(0,0,0,0.3)
}




.listaVacia {
    display: flex;
    flex-direction: column;
    width: 1000px;
    position: absolute;
    margin-bottom: -100px;
}
.bx-sad {
    font-size: 5rem;
    color: #3b7a83;
    margin-bottom: 20px;
    text-align: center;
}
.listaVacia > p {
    font-size: 1.2rem;
    color: #4c4c4c;
    font-weight: 600;
    text-align: center;
}
